interface DoctrinePillProps {
  label: string
  code: 'I' | 'T' | 'H' | 'AI'
  className?: string
}

const codeMap: Record<string, string> = {
  I:  'Intellectual',
  T:  'Tactical',
  H:  'Human',
  AI: 'AI-forward',
}

/**
 * DoctrinePill — 4-pronged doctrine indicator.
 * Renders a monospace pill with a dot and the code's full meaning as aria-label.
 */
export default function DoctrinePill({ label, code, className = '' }: DoctrinePillProps) {
  return (
    <span
      className={`doctrine-pill ${className}`}
      title={codeMap[code] || label}
      aria-label={`${codeMap[code] || label} — ${label}`}
    >
      <span className="pill-dot" aria-hidden="true" />
      <span>{code}</span>
      <span className="hidden sm:inline text-[var(--color-muted)] normal-case tracking-normal" style={{ fontFamily: 'var(--font-mono)', textTransform: 'none', letterSpacing: 0 }}>
        {' '}· {label}
      </span>
    </span>
  )
}
