interface ImpactMetricProps {
  value: string
  label: string
  note?: string
  className?: string
}

/**
 * ImpactMetric — displays a number/value with a label and optional confidence note.
 * Styled like an operational dashboard readout.
 */
export default function ImpactMetric({ value, label, note, className = '' }: ImpactMetricProps) {
  return (
    <div className={`impact-metric ${className}`} role="figure" aria-label={`${label}: ${value}`}>
      <div className="impact-metric-value" aria-hidden="true">{value}</div>
      <div className="impact-metric-label">{label}</div>
      {note && <div className="impact-metric-note">{note}</div>}
    </div>
  )
}
